import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, NavController } from '@ionic/angular';
import { ApiservicesService } from 'src/app/provider/apiservices.service';
import { DataTransferService } from 'src/app/services/data-transfer.service';
import { LocalStorageService } from 'src/app/services/local-storage.service';
import { URLS } from 'src/assets/constant';

@Component({
  selector: 'app-myactivity-detail',
  templateUrl: './myactivity-detail.page.html',
  styleUrls: ['./myactivity-detail.page.scss'],
})
export class MyactivityDetailPage implements OnInit {
  errorMessage: string;
  myActvity: any;
  myActvityData: any;
  previousActvity: any;
  slideOpts = {
    initialSlide: 0,
    slidesPerView: 2.5,
    speed: 400,
    spaceBetween: 5,
  };
  RemarkText: any = '';
  constructor(private zone: NgZone, private apiservices: ApiservicesService, private stoargeServices: LocalStorageService,
    private sendData: DataTransferService, private router: Router, private navctrl: NavController,
    private alertController:AlertController) { }

  ngOnInit() {
    this.previousActvity = this.sendData.alldata;
    // this.myactvityFun();
  }
  ionViewWillEnter(){
    this.myactvityFun();
  }

  async presentAlertConfirm(item){  
    const alert = await this.alertController.create({
      // header: 'Confirm!',
      message: 'Are you sure you want to'+' '+item.button_text+ ' ' +'this award nomination?',
      buttons: [{
        text: 'No',
        role: 'cancel',
        cssClass: 'secondary',
        handler: (blah) => {}
      }, {
        text: 'Yes',
        handler: () => {
        this.RejectAcceptFun(item);
        }
      }]
    });  
    await alert.present();
  }

  myactvityFun() {
    this.zone.run(async () => {
      this.apiservices.showLoader();
      this.stoargeServices.getStorage().then(storedData => {
        let apiKey = {
          employee_id: storedData.employeeID,
          award_id: this.previousActvity.award_id,
          nomination_id: this.previousActvity.auto_id

        }
        this.apiservices.apiCallWithLoginToken(URLS.myactvityApi, apiKey).subscribe((result) => {
          this.apiservices.hideLoader();
          this.myActvity = result;
          if (result.status == 1) {
            this.myActvityData = result.data;
            this.errorMessage = '';
          } else {
            this.myActvityData={};
            this.errorMessage = result.message;
            this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
          }

        }, err => {
          this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
          this.apiservices.hideLoader();
        });


      });
    });
  }

  RejectAcceptFun(item) {
    if (item.citation_required) {
      if (this.RemarkText.trim() == '') {
        return (this.apiservices.showToastMessage('Please enter remark', 'top', 3000, 'redBg'));
      }
    }
    this.zone.run(async () => {
      this.apiservices.showLoader();
      this.stoargeServices.getStorage().then(storedData => {
        let apiKey = {
          employee_id: storedData.employeeID,
          award_id: this.previousActvity.award_id,
          nomination_id: this.previousActvity.auto_id,
          approval_level: this.myActvity.approval_level,
          action: item.value,
          citation: this.RemarkText
        }
        let copy_apiKey = {
          employee_id: storedData.employeeID,
          award_id: this.previousActvity.award_id,
          nomination_id: this.previousActvity.auto_id,
          approval_level: this.myActvity.approval_level,
          action: item.value,
          citation: this.RemarkText

        }
        this.apiservices.apiCallWithLoginTokenComment(URLS.nominationActionApi, apiKey, copy_apiKey).subscribe((result) => {
          this.apiservices.hideLoader();
          if (result.status == 1) {
            this.navctrl.pop();
          } else {
            this.apiservices.showToastMessage(result.message, 'top', 3000, 'redBg');
          }

        }, err => {
          this.apiservices.showToastMessage(JSON.stringify(err), 'top', 3000, 'redBg');
          this.apiservices.hideLoader();
        });


      });
    });

  }

  gotoEdit(){
    this.sendData.alldata={mainData:this.myActvity,nominationId:this.previousActvity.auto_id};
    this.router.navigate(['/award-employee-list']);
  }

}
